.landingPage{display:flex;justify-content:center;overflow:hidden}.landingPage .content{transform:translateY(20%);padding:1rem;width:100%;margin:0 1rem;max-width:35rem;text-align:center}.landingPage .content .logo{width:80%;height:6rem;object-fit:cover}.landingPage .content .siteTitle{padding:1rem;color:#2a2a2a;font-weight:lighter;font-family:"Rakkas", cursive;letter-spacing:0;font-size:5rem}.landingPage .fullscreenSearch{display:none}@media only screen and (max-width: 480px){.landingPage .content .logo{height:3.3rem}.landingPage .content .siteTitle{font-size:3rem}.landingPage .fullscreenSearch{display:block;position:absolute;left:0;top:0;z-index:100;height:100%;width:100%;background:#f6f6f6}.landingPage .fullscreenSearch #searchbar .suggestions{border-radius:0}.landingPage .fullscreenSearch #searchbar .back{position:absolute;left:0;top:50%;transform:translateY(-50%);padding-left:0.25rem}.landingPage .fullscreenSearch #searchbar .back ion-icon{font-size:1.6rem}.landingPage .fullscreenSearch #searchbar input{padding-left:2.25rem;border-radius:0}}
